$white: #fff;
$buttonBG: #c9453b;
$black: #000;

//components

html {
  font-size: 16px;
}

body {
  position: relative;
}

.container {
  width: 94%;
  max-width: 1152px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
}

.paragraph {
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  font-style: normal;
  font-weight: normal;
  line-height: 160%;

  &_white {
    color: $white;
  }
}

.button {
  display: inline-block;
  padding: 15px 35px;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  color: $white;
  cursor: pointer;
  background-color: $buttonBG;
  border-radius: 5px;
  transition: box-shadow 0.3s, transform 0.3s;

  &:hover {
    box-shadow: 3px 3px 5px 0 rgba(133, 133, 133, 0.6), 3px 3px 5px -2px rgba(255, 255, 255, 0.6) inset;
    transition: box-shadow 0.3s, transform 0.3s;
    transform: scale(1.02);
  }
}

.title-section {
  font-family: "Gelasio", sans-serif;
  font-size: 2.68rem;
  font-style: normal;
  font-weight: 500;
  line-height: 140%;
  color: $black;
}

.title-item {
  font-family: "Gelasio", sans-serif;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  color: $black;
}

.swiper-button-prev,
.popular-location-prev {
  position: absolute;
  top: auto;
  bottom: 40px;
  left: 25%;
  z-index: 5;
  display: inline-block;
  width: 32px;
  height: 32px;
  color: transparent;
  cursor: pointer;
  background-image: url("../images/arrow-left.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.swiper-button-next,
.popular-location-next {
  position: absolute;
  top: auto;
  right: 25%;
  bottom: 40px;
  z-index: 5;
  display: inline-block;
  width: 32px;
  height: 32px;
  color: transparent;
  cursor: pointer;
  background-image: url("../images/arrow-right.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.special-offers > .swiper-scrollbar,
.popular-location > .popular-location-scrollbar {
  position: absolute;
  top: auto;
  bottom: 50px;
  left: 30%;
  z-index: 5;
  display: inline-block;
  width: 40%;
  height: 10px;
  background-color: #c9453b28;
}

.swiper-scrollbar-drag,
.locations-drag {
  z-index: 5;
  height: 100%;
  cursor: pointer;
  background-color: $buttonBG;
  border-radius: 10px;
}

@media screen and (max-width: 700px) {
  .swiper-button-prev,
  .popular-location-prev {
    left: 10%;
  }

  .swiper-button-next,
  .popular-location-next {
    right: 10%;
  }

  .special-offers > .swiper-scrollbar,
  .popular-location > .popular-location-scrollbar {
    left: 25%;
    width: 50%;
  }
}

.popular-location-prev,
.popular-location > .popular-location-scrollbar,
.popular-location-next {
  display: none;
}

@media screen and (max-width: 550px) {
  html {
    font-size: 14px;
  }
}
