$width: 32px;
$height: 32px;
$wrapCoeff: 1.5;
$solid: 4px;
$colorBG: #ffffffc2;
$colorSolid: #c9453b;
$borRad: 50%;
$anDuration: 0.3s;

.burger {
  position: fixed;
  top: 27px;
  right: 3%;
  z-index: 20;
  display: none;
  align-items: center;
  justify-content: center;
  width: $width * $wrapCoeff;
  height: $height * $wrapCoeff;
  cursor: pointer;
  background-color: $colorBG;
  border-radius: $borRad;

  &__middle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: $width;
    height: $solid;
    margin: ($height - $solid) / 2 0;
    background-color: $colorSolid;
    background-size: 20px 10px;
    transition: $anDuration;
  }

  &__top {
    position: absolute;
    top: $height / 2.5;
    left: $width / 4;
    z-index: 100;
    display: inline-block;
    width: $width;
    height: $solid;
    background-color: $colorSolid;
    transition: top $anDuration, transform $anDuration;
  }

  &__bottom {
    position: absolute;
    bottom: $height / 2.5;
    left: $width / 4;
    z-index: 100;
    display: inline-block;
    width: $width;
    height: $solid;
    background-color: $colorSolid;
    transition: top $anDuration, transform $anDuration;
  }
}

.burger_open > .burger__middle {
  background-color: transparent;
  transition: $anDuration;
}

.burger_open > .burger__top {
  top: $height / $wrapCoeff;
  transition: top $anDuration, transform $anDuration;
  transform: rotate(45deg);
}

.burger_open > .burger__bottom {
  top: $height / $wrapCoeff;
  transition: top $anDuration, transform $anDuration;
  transform: rotate(-45deg);
}

@media screen and (max-width: 950px) {
  .burger {
    display: flex;
  }
}
