.special-offers {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 70px 0;
  overflow: hidden;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    display: inline-block;
    width: calc((100% - (30px * 4)) / 5);
    height: 100%;
    content: "";
    background-color: rgba(255, 255, 255, 0.5);
  }

  &::after {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    display: inline-block;
    width: calc((100% - (30px * 4)) / 5);
    height: 100%;
    content: "";
    background-color: rgba(255, 255, 255, 0.5);
  }

  // &__title {
  // }

  &__pharagraph {
    max-width: 560px;
    padding-top: 20px;
    text-align: center;
  }

  &__items {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 60px 0;
  }

  &__item {
    display: grid;
    flex-shrink: 0;
    grid-template-rows: repeat(5, auto);
    grid-template-columns: max-content auto;
    row-gap: 10px;
    // max-width: calc((100% - (30px * 2)) / 5);
    cursor: pointer;
  }

  &__images {
    display: inline-block;
    grid-area: 1/1/2/3;
    width: 100%;
    object-fit: contain;
  }

  &__item-title {
    grid-area: 2/1/3/3;
    width: 100%;
    padding-top: 10px;
    font-family: "Gelasio", sans-serif;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    color: $black;
  }

  &__state {
    position: relative;
    grid-area: 3/1/4/2;
    padding-left: 24px;
    font-family: "Roboto", sans-serif;
    font-size: 1.25rem;
    font-style: normal;
    font-weight: normal;
    line-height: 150%;
    color: $black;
    opacity: 0.6;
  }

  &__state::before {
    position: absolute;
    top: 7px;
    left: 7px;
    width: 11px;
    height: 15px;
    content: "";
    background-image: url("../images/special-offers/location.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &__rate {
    position: relative;
    grid-area: 3/2/4/3;
    padding-left: 24px;
    font-family: "Roboto", sans-serif;
    font-size: 1.125rem;
    font-style: normal;
    font-weight: normal;
    line-height: 150%;
    color: $black;
  }

  &__rate::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 24px;
    height: 24px;
    content: "";
    background-image: url("../images/special-offers/Star.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &__duration {
    grid-area: 4/1/5/2;
    font-family: "Roboto", sans-serif;
    font-size: 1rem;
    font-style: normal;
    font-weight: normal;
    line-height: 150%;
    color: $black;
    opacity: 0.6;
  }

  &__cost {
    grid-area: 5/1/6/2;
    font-family: "Roboto", sans-serif;
    font-size: 1rem;
    font-style: normal;
    font-weight: normal;
    line-height: 150%;
    color: rgba(0, 0, 0, 0.6);
  }

  &__cost::before {
    font-size: 1.1rem;
    font-weight: bold;
    content: "$";
  }
}

@media screen and (max-width: 1440px) {
  .special-offers {
    padding: 70px 6%;

    &::before,
    &::after {
      width: calc(((100% - (30px * 4)) / 4) / 4);
    }

    &__items {
      width: 100%;
    }

    &__item {
      max-width: calc((100% - (30px * 2)) / 4);
    }
  }
}

@media screen and (max-width: 1300px) {
  .special-offers {
    &::before,
    &::after {
      width: calc(((100% - (30px * 4)) / 4) / 4);
    }

    &__item {
      max-width: calc((100% - (30px * 2)) / 3);
    }
  }
}

@media screen and (max-width: 950px) {
  .special-offers {
    &::before,
    &::after {
      width: calc(((100% - (30px * 4)) / 4) / 4);
    }

    &__item {
      max-width: calc((100% - 30px) / 2);
    }
  }
}

@media screen and (max-width: 630px) {
  .special-offers {
    &::before,
    &::after {
      width: calc(((100% - (30px * 4)) / 4) / 4);
    }

    &__item {
      max-width: 100%;
    }
  }
}
