.popular-location {
  position: relative;
  padding: 140px 0 70px;

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    text-align: center;
  }

  // &__title {}
  &__desctiption {
    max-width: 735px;
  }

  &__items {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding-top: 50px;
    padding-bottom: 20px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 32%;
    max-width: 360px;
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    box-shadow: 0 6px 35px rgba(0, 0, 0, 0.08);
    transition: box-shadow 0.3s;

    &:hover {
      box-shadow: 4px 4px 8px 0 rgba(34, 60, 80, 0.2);
      transition: box-shadow 0.3s;
    }
  }

  &__illustration {
    display: inline-block;
    width: 100%;
    height: auto;
    object-fit: cover;
  }

  &__title-item {
    padding: 35px;
    padding-bottom: 10px;
  }

  &__item-description {
    padding: 0 35px 35px;
  }
}

@media screen and (max-width: 900px) {
  .popular-location {
    padding: 140px 0;

    &__item {
      flex-shrink: 0;
      max-width: initial;
    }
  }

  .popular-location-prev,
  .popular-location > .popular-location-scrollbar,
  .popular-location-next {
    display: initial;
  }
}
