.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: max-content;
    padding-top: 35px;
  }

  &__logo {
    font-family: "Ribeye", sans-serif;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    color: $white;
    letter-spacing: 0.06em;
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 805px;
    margin-left: 50px;
  }

  &__nav-list {
    display: flex;
    justify-content: space-between;
    width: max-content;
    height: 100%;
  }

  &__nav-item {
    margin-right: 50px;
  }

  &__nav-link {
    font-family: "Roboto", sans-serif;
    font-size: 1.125rem;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    color: $white;
  }

  // &__btn { }
}

@media screen and (max-width: 950px) {
  .header {
    &__nav {
      display: none;
      margin-left: 0;
    }

    &__nav_active {
      position: fixed;
      top: 0;
      bottom: auto;
      left: 0;
      z-index: 10;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100vw;
      max-width: initial;
      height: 100vh;
      overflow-y: auto;
      background-color: #c9453bf3;
    }

    &__nav-list {
      position: relative;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: max-content;
      margin-top: 35px;
    }

    &__nav-item {
      margin-right: 0;
      margin-bottom: 20px;
    }

    &__nav-link {
      display: inline-block;
      height: 42px;
      line-height: 42px;
    }

    &__btn {
      display: none;
    }

    &__btn_active {
      display: inline-block;
      color: $buttonBG;
      background-color: $white;
      border: 1px solid $buttonBG;
    }
  }
}
