.blog {
  padding-top: 100px;

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    text-align: center;
  }

  &__paragraph {
    max-width: 630px;
    margin: 20px auto;
    text-align: center;
  }

  &__items {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    padding: 40px 0;
  }

  &__item {
    display: flex;
    grid-column: 2/3;
    // max-width: 544px;
  }

  &__item-illustration {
    width: 50%;
    object-fit: cover;
  }

  &__text {
    display: flex;
    flex-direction: column;
    padding: 30px;
    padding-right: 10px;
  }

  &__item-title {
    padding-bottom: 10px;
    color: $black;
  }

  &__item-description {
    display: inline-block;
    max-height: 4rem;
    padding-bottom: 5px;
    overflow: hidden;
    color: $black;
  }

  &__item-autor {
    font-family: "Roboto", sans-serif;
    font-size: 1.25rem;
    font-style: normal;
    font-weight: normal;
    line-height: 150%;
    color: $black;
    opacity: 0.8;
  }

  &__btn-more {
    display: none;
  }
}

.blog__item_main {
  flex-direction: column;
  grid-row: 1/4;
  grid-column: 1/2;
}

.blog__item-illustration_main {
  width: 100%;
}

.blog__text_main {
  padding-left: 0;
}

.blog__item-description_main {
  max-height: 9rem;
}

@media screen and (max-width: 1000px) {
  .blog {
    padding-top: 70px;

    &__items {
      grid-template-rows: max-content 1fr;
      grid-template-columns: 1fr 1fr 1fr;
      padding: 30px 0;
    }

    &__item {
      flex-direction: column;
      grid-row: 2/3;
      grid-column: initial;
    }

    &__item-illustration {
      width: 100%;
    }

    &__text {
      padding-left: 0;
    }
  }

  .blog__item_main {
    flex-direction: column;
    grid-row: 1/2;
    grid-column: 1/4;
  }
}

@media screen and (max-width: 770px) {
  .blog {
    &__items {
      display: flex;
      flex-direction: column;
    }

    &__item {
      display: none;
    }

    &__item:first-child {
      display: flex;
    }

    &__item_active {
      display: flex;
    }

    &__btn-more {
      display: inline-block;
    }
  }
}
