* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 100%;
  font-size: 14px;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

@font-face {
  font-family: "Gelasio";
  font-style: normal;
  font-weight: 500;
  src: local("Gelasio"), url("../fonts/Gelasio-Medium.woff2") format("woff2");
}

@font-face {
  font-family: "Gelasio";
  font-style: normal;
  font-weight: 700;
  src: local("Gelasio"), url("../fonts/Gelasio-Bold.woff2") format("woff2");
}

@font-face {
  font-family: "Ribeye";
  font-style: normal;
  font-weight: 400;
  src: local("Ribeye"), url("../fonts/Ribeye-Regular.woff2") format("woff2");
}

html {
  font-size: 16px;
}

body {
  position: relative;
}

.container {
  width: 94%;
  max-width: 1152px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
}

.paragraph {
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  font-style: normal;
  font-weight: normal;
  line-height: 160%;
}

.paragraph_white {
  color: #fff;
}

.button {
  display: inline-block;
  padding: 15px 35px;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  color: #fff;
  cursor: pointer;
  background-color: #c9453b;
  border-radius: 5px;
  transition: box-shadow 0.3s, transform 0.3s;
}

.button:hover {
  box-shadow: 3px 3px 5px 0 rgba(133, 133, 133, 0.6), 3px 3px 5px -2px rgba(255, 255, 255, 0.6) inset;
  transition: box-shadow 0.3s, transform 0.3s;
  transform: scale(1.02);
}

.title-section {
  font-family: "Gelasio", sans-serif;
  font-size: 2.68rem;
  font-style: normal;
  font-weight: 500;
  line-height: 140%;
  color: #000;
}

.title-item {
  font-family: "Gelasio", sans-serif;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  color: #000;
}

.swiper-button-prev,
.popular-location-prev {
  position: absolute;
  top: auto;
  bottom: 40px;
  left: 25%;
  z-index: 5;
  display: inline-block;
  width: 32px;
  height: 32px;
  color: transparent;
  cursor: pointer;
  background-image: url("../images/arrow-left.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.swiper-button-next,
.popular-location-next {
  position: absolute;
  top: auto;
  right: 25%;
  bottom: 40px;
  z-index: 5;
  display: inline-block;
  width: 32px;
  height: 32px;
  color: transparent;
  cursor: pointer;
  background-image: url("../images/arrow-right.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.special-offers > .swiper-scrollbar,
.popular-location > .popular-location-scrollbar {
  position: absolute;
  top: auto;
  bottom: 50px;
  left: 30%;
  z-index: 5;
  display: inline-block;
  width: 40%;
  height: 10px;
  background-color: #c9453b28;
}

.swiper-scrollbar-drag,
.locations-drag {
  z-index: 5;
  height: 100%;
  cursor: pointer;
  background-color: #c9453b;
  border-radius: 10px;
}

@media screen and (max-width: 700px) {
  .swiper-button-prev,
  .popular-location-prev {
    left: 10%;
  }
  .swiper-button-next,
  .popular-location-next {
    right: 10%;
  }
  .special-offers > .swiper-scrollbar,
  .popular-location > .popular-location-scrollbar {
    left: 25%;
    width: 50%;
  }
}

.popular-location-prev,
.popular-location > .popular-location-scrollbar,
.popular-location-next {
  display: none;
}

@media screen and (max-width: 550px) {
  html {
    font-size: 14px;
  }
}

.burger {
  position: fixed;
  top: 27px;
  right: 3%;
  z-index: 20;
  display: none;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  cursor: pointer;
  background-color: #ffffffc2;
  border-radius: 50%;
}

.burger__middle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 4px;
  margin: 14px 0;
  background-color: #c9453b;
  background-size: 20px 10px;
  transition: 0.3s;
}

.burger__top {
  position: absolute;
  top: 12.8px;
  left: 8px;
  z-index: 100;
  display: inline-block;
  width: 32px;
  height: 4px;
  background-color: #c9453b;
  transition: top 0.3s, transform 0.3s;
}

.burger__bottom {
  position: absolute;
  bottom: 12.8px;
  left: 8px;
  z-index: 100;
  display: inline-block;
  width: 32px;
  height: 4px;
  background-color: #c9453b;
  transition: top 0.3s, transform 0.3s;
}

.burger_open > .burger__middle {
  background-color: transparent;
  transition: 0.3s;
}

.burger_open > .burger__top {
  top: 21.33333px;
  transition: top 0.3s, transform 0.3s;
  transform: rotate(45deg);
}

.burger_open > .burger__bottom {
  top: 21.33333px;
  transition: top 0.3s, transform 0.3s;
  transform: rotate(-45deg);
}

@media screen and (max-width: 950px) {
  .burger {
    display: flex;
  }
}

.header {
