  top: 0;
  left: 0;
  width: 100%;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: max-content;
  padding-top: 35px;
}

.header__logo {
  font-family: "Ribeye", sans-serif;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  color: #fff;
  letter-spacing: 0.06em;
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 805px;
  margin-left: 50px;
}

.header__nav-list {
  display: flex;
  justify-content: space-between;
  width: max-content;
  height: 100%;
}

.header__nav-item {
  margin-right: 50px;
}

.header__nav-link {
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  color: #fff;
}

@media screen and (max-width: 950px) {
  .header__nav {
    display: none;
    margin-left: 0;
  }
  .header__nav_active {
    position: fixed;
    top: 0;
    bottom: auto;
    left: 0;
