    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    max-width: initial;
    height: 100vh;
    overflow-y: auto;
    background-color: #c9453bf3;
  }
  .header__nav-list {
    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: max-content;
    margin-top: 35px;
  }
  .header__nav-item {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .header__nav-link {
    display: inline-block;
    height: 42px;
    line-height: 42px;
  }
  .header__btn {
