.railtrips {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100vh;
  min-height: 550px;
  background-image: url("../images/railtrips.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}

.railtrips__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 94%;
  height: 100%;
}

.railtrips__title {
  display: inline-block;
  padding-bottom: 30px;
  font-family: "Gelasio", sans-serif;
  font-size: 4.06rem;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  color: #fff;
}

.railtrips__paragraph {
  max-width: 458px;
  padding-bottom: 40px;
}

@media screen and (max-width: 900px) {
  .railtrips {
    background-position: right top;
  }
  .railtrips__title {
    font-size: 2.5rem;
  }
}

@media screen and (max-width: 550px) {
  .railtrips__title {
    font-size: 2rem;
    text-shadow: 4px 4px 2px rgba(0, 0, 0, 0.81);
  }
  .railtrips__paragraph {
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.81);
  }
}

.popular-location {
  position: relative;
  padding: 140px 0 70px;
}

.popular-location__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  text-align: center;
}

.popular-location__desctiption {
  max-width: 735px;
}

.popular-location__items {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-top: 50px;
  padding-bottom: 20px;
}

.popular-location__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 32%;
  max-width: 360px;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  box-shadow: 0 6px 35px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.3s;
}

.popular-location__item:hover {
  box-shadow: 4px 4px 8px 0 rgba(34, 60, 80, 0.2);
  transition: box-shadow 0.3s;
}

.popular-location__illustration {
  display: inline-block;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.popular-location__title-item {
  padding: 35px;
  padding-bottom: 10px;
}

.popular-location__item-description {
  padding: 0 35px 35px;
}

@media screen and (max-width: 900px) {
  .popular-location {
    padding: 140px 0;
  }
  .popular-location__item {
    flex-shrink: 0;
    max-width: initial;
  }
  .popular-location-prev,
  .popular-location > .popular-location-scrollbar,
  .popular-location-next {
    display: initial;
  }
}

.special-offers {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 70px 0;
  overflow: hidden;
}

.special-offers::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  display: inline-block;
  width: calc((100% - (30px * 4)) / 5);
  height: 100%;
  content: "";
  background-color: rgba(255, 255, 255, 0.5);
}

.special-offers::after {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 5;
  display: inline-block;
  width: calc((100% - (30px * 4)) / 5);
  height: 100%;
  content: "";
  background-color: rgba(255, 255, 255, 0.5);
}

.special-offers__pharagraph {
  max-width: 560px;
  padding-top: 20px;
  text-align: center;
}

.special-offers__items {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 60px 0;
}

.special-offers__item {
  display: grid;
  flex-shrink: 0;
  grid-template-rows: repeat(5, auto);
  grid-template-columns: max-content auto;
  row-gap: 10px;
  cursor: pointer;
}

.special-offers__images {
  display: inline-block;
  grid-area: 1/1/2/3;
  width: 100%;
  object-fit: contain;
}

.special-offers__item-title {
  grid-area: 2/1/3/3;
  width: 100%;
  padding-top: 10px;
  font-family: "Gelasio", sans-serif;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  color: #000;
}

.special-offers__state {
  position: relative;
  grid-area: 3/1/4/2;
  padding-left: 24px;
  font-family: "Roboto", sans-serif;
  font-size: 1.25rem;
  font-style: normal;
  font-weight: normal;
  line-height: 150%;
  color: #000;
  opacity: 0.6;
}

.special-offers__state::before {
  position: absolute;
  top: 7px;
  left: 7px;
  width: 11px;
  height: 15px;
  content: "";
  background-image: url("../images/special-offers/location.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.special-offers__rate {
  position: relative;
  grid-area: 3/2/4/3;
  padding-left: 24px;
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  font-style: normal;
  font-weight: normal;
  line-height: 150%;
  color: #000;
}

.special-offers__rate::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 24px;
  content: "";
  background-image: url("../images/special-offers/Star.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.special-offers__duration {
  grid-area: 4/1/5/2;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: normal;
  line-height: 150%;
  color: #000;
  opacity: 0.6;
}

.special-offers__cost {
  grid-area: 5/1/6/2;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: normal;
  line-height: 150%;
  color: rgba(0, 0, 0, 0.6);
}

.special-offers__cost::before {
  font-size: 1.1rem;
  font-weight: bold;
  content: "$";
}

@media screen and (max-width: 1440px) {
  .special-offers {
    padding: 70px 6%;
  }
  .special-offers::before, .special-offers::after {
    width: calc(((100% - (30px * 4)) / 4) / 4);
  }
  .special-offers__items {
    width: 100%;
  }
  .special-offers__item {
    max-width: calc((100% - (30px * 2)) / 4);
  }
}

@media screen and (max-width: 1300px) {
  .special-offers::before, .special-offers::after {
    width: calc(((100% - (30px * 4)) / 4) / 4);
  }
  .special-offers__item {
    max-width: calc((100% - (30px * 2)) / 3);
  }
}

@media screen and (max-width: 950px) {
  .special-offers::before, .special-offers::after {
    width: calc(((100% - (30px * 4)) / 4) / 4);
  }
  .special-offers__item {
    max-width: calc((100% - 30px) / 2);
  }
}

@media screen and (max-width: 630px) {
  .special-offers::before, .special-offers::after {
    width: calc(((100% - (30px * 4)) / 4) / 4);
  }
  .special-offers__item {
    max-width: 100%;
  }
}

.blog {
  padding-top: 100px;
}

.blog__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.blog__title {
  text-align: center;
}

.blog__paragraph {
  max-width: 630px;
  margin: 20px auto;
  text-align: center;
}

