.railtrips {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100vh;
  min-height: 550px;
  background-image: url("../images/railtrips.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 94%;
    height: 100%;
  }

  &__title {
    display: inline-block;
    padding-bottom: 30px;
    font-family: "Gelasio", sans-serif;
    font-size: 4.06rem;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
    color: $white;
  }

  &__paragraph {
    max-width: 458px;
    padding-bottom: 40px;
  }
}

@media screen and (max-width: 900px) {
  .railtrips {
    background-position: right top;

    &__title {
      font-size: 2.5rem;
    }
  }
}

@media screen and (max-width: 550px) {
  .railtrips {
    &__title {
      font-size: 2rem;
      text-shadow: 4px 4px 2px rgba(0, 0, 0, 0.81);
    }

    &__paragraph {
      text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.81);
    }
  }
}