.footer {
  &__wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 70px 0;
  }

  &__logo {
    position: absolute;
    top: 70px;
    left: 0;
    font-family: "Ribeye", sans-serif;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    color: $black;
    letter-spacing: 0.06em;
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    width: max-content;
    padding-top: 75px;
  }

  &__title,
  &__mail {
    padding-bottom: 10px;
    font-family: "Roboto", sans-serif;
    font-size: 1rem;
    font-style: normal;
    font-weight: normal;
    line-height: 160%;
    color: $black;
    letter-spacing: 0.06em;
  }

  &__media-links {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 10px 0;
  }

  &__item-media {
    display: inline-block;
    width: 36px;
    height: 36px;
  }

  &__item-media:not(:last-child) {
    margin-right: 15px;
  }

  &__link-media {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 28px;
  }

  &__item-media:nth-child(1) &__link-media {
    background-image: url("../images/socials-media/facebook.png");
  }

  &__item-media:nth-child(2) &__link-media {
    background-image: url("../images/socials-media/twitter.png");
  }

  &__item-media:nth-child(3) &__link-media {
    background-image: url("../images/socials-media/google.png");
  }

  &__item-media:nth-child(4) &__link-media {
    background-image: url("../images/socials-media/inst.png");
  }

  &__links {
    display: grid;
    grid-template-rows: repeat(5, 1fr);
    grid-template-columns: 1fr 1fr;
    row-gap: 20px;
  }

  &__link {
    font-family: "Roboto", sans-serif;
    font-size: 1rem;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    color: $black;
    letter-spacing: 0.06em;
  }

  &__subscribe {

  }

  &__subscribe-title {
    font-family: "Roboto", sans-serif;
    font-size: 1.25rem;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    color: $black;
    letter-spacing: 0.06em;
  }

  &__form {
    display: flex;
    height: max-content;
    padding: 40px 0;
  }

  &__input {
    min-width: 195px;
    height: 100%;
    padding: 14px 20px;
    margin-right: 35px;
    font-family: "Roboto", sans-serif;
    font-size: 1rem;
    font-style: normal;
    font-weight: normal;
    line-height: 150%;
    color: $black;
    letter-spacing: 0.06em;
    border: 1px solid rgba(0, 0, 0, 0.3);
  }

  // &__btn {
  // }
}

@media screen and (max-width: 1100px) {
  .footer {
    &__wrapper {
      flex-wrap: wrap;
      padding: 0;
      padding-top: 45px;
      margin: 50px auto;
    }

    &__logo {
      top: 0;
    }

    &__links {
      justify-items: center;
      order: 0;
      width: 100%;
      padding: 35px 0;
    }

    &__contacts {
      order: 1;
      padding-top: 0;
    }

    &__form {
      flex-direction: column;
      order: 2;
      padding: 25px 0;
    }

    &__input {
      width: 100%;
      margin-bottom: 20px;
    }

    &__btn {
      width: 100%;
    }
  }
}

@media screen and (max-width: 600px) {
  .footer {
    &__wrapper {
      flex-direction: column;
      padding-top: 35px;
    }
  }
}
